
class SharedMemory {
    memory = {};
    register(name = "", state = {}, updater = () => { }) {
        this.memory[name] = { state, updater };
    }
    remove(name = "") {
        delete this.memory[name];
    }
    get(name = "") {
        let m = this.memory[name];
        if (typeof m === "undefined")
            return null;
        else
            return m.state;
    }
    update(name = "", data = {}) {
        let m = this.memory[name];
        if (typeof m === "undefined")
            return;
        m.updater(data);
    }
}

const sharedMemory = new SharedMemory();

export default sharedMemory;